@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,400;1,700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

.App {
    padding: 30px;
}

.block {
    height: 10px;
    background: black;
    transition: 0.2s all;
}

.inputs {
    display: flex;
    gap: 50px;

    &-header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 5px;
    }

    &-text input {
        width: 60px;
        text-align: center;
    }
}

.diagram {
    margin-top: 40px;
    display: flex;
    flex-direction: column;


    &__item {
        display: flex;
        gap: 15px;

        &-text {
            display: flex;
            gap: 5px;
            align-items: center;
            justify-content: space-between;
            width: 145px;
        }

        &-name {
            line-height: 27px;
        }
    }

    &__graph {
        width: 600px;
        display: flex;
        align-items: center;
        gap: 10px;
        border-left: 5px solid red;
    }

    img {
        width: 25px;
        height: 25px;
    }

    .form {
        div {
            display: flex;
        }

        display: flex;
        align-items: center;
        gap: 10px;
    }

    label, input {
        display: block;
        font-size: 10px;
    }

}

@media (max-width: 512px) {
   .inputs {
       flex-direction: column;
       gap: 20px;
       &-header {
           width: 250px;
       }
   }

    .diagram {
        &__item {
            gap: 5px;
            width: 500px;
            &-text {
                width: 220px;
            }
        }

        &__graph {

        }

        .form {
            flex-direction: column;
            gap: 0;
            align-items: flex-start;
        }
    }
}

